import Logger from './../Logger';
import PublicEvent from './PublicEvent';

/**
 * HandleStreamOfferEvent
 *
 * Update our remote description, create an answer and resolve with new sdp.
 * Also register a cb for when the new remote stream is available.
 **/
class HandleStreamOfferEvent extends PublicEvent {
  handle(msg) {
    if (!this.context._session || !this.context._connection) {
      Logger.warn('HandleStreamOfferEvent::no session or connection.');
      return;
    }
    const { _connection, _session } = this.context;
    const pc = _session.peerConnection;

    // Note: .then(pc.createAnswer) causes "Illegal invocation" error on chrome
    // for windows. That's why the more verbose answer => pc.createAnswer
    // are needed.
    new Promise((resolve, reject) => {
      pc.setRemoteDescription(new RTCSessionDescription(msg.sdp))
        .then(answer => pc.createAnswer(answer))
        .then(answer => pc.setLocalDescription(answer))
        .then(() => resolve(pc.localDescription))
        .catch(reject);
    })
      .then(sdp => _connection.send({ type: 'stream_answer', sdp: sdp }))
      .then(() => _session.remoteDescriptionUpdate(msg.sdp))
      .catch(Logger.error);
  }
}

export default HandleStreamOfferEvent;
