import PublicEvent from './PublicEvent';
import { hasVideo } from '../utils/StreamHelpers';

class AcceptSessionEvent extends PublicEvent {
  handle(msg) {
    const {
      type,
      session: { sessionDescriptionHandler }
    } = msg;
    const localStream = sessionDescriptionHandler.getLocalStream();

    super.handle({
      type: type,
      localStream: localStream,
      remoteStream: sessionDescriptionHandler.getRemoteStream()
    });

    this.context._connection.send({
      type: 'mute_video',
      on: !hasVideo(localStream)
    });
  }
}

export default AcceptSessionEvent;
