import Logger from './Logger';
import debounce from './utils/debounce';

/**
 * Sound Meter that generates a number correlated to audio volume. The meter
 * itself displays nothing, but it makes the instantaneous and time-decaying
 * volumes available for inspection. It also reports on the fraction of samples
 * that were at or near the top of the measurement range.
 **/
class SoundMeter {
  constructor() {
    this.level = 0;
    this.instant = 0.0;
    this.intervalCounter = 0;
    this.silenceDuration = 0;
    this.init();
    this.offUpdate = this.offUpdate.bind(this);
  }

  /**
   * Initialize audio context and prepare script processor to read input
   * channel data.
   **/
  init() {
    const AudioContext = window.AudioContext || window.webkitAudioContext;
    if (typeof AudioContext === 'undefined') {
      Logger.error(
        'AudioContext is not available. Probably the current' +
          ' user agent does not support this feature. Use the feature detector' +
          ' to hide not supported elements. Any call on onUpdate will not' +
          ' send values.'
      );
      return;
    }

    this.context = new AudioContext();

    this.context.onstatechange = () => {
      if (this.context.state === 'suspended') {
        Logger.error('SoundMeter::init AudioContext: ', this.context.state);
      }
    };

    this.script = this.context.createScriptProcessor(2048, 1, 1);
    this.script.onaudioprocess = event => {
      const input = event.inputBuffer.getChannelData(0);
      const sum = input.reduce((prev, current) => {
        return prev + (current * current);
      }, 0);
      this.instant = Math.sqrt(sum / input.length) * 100;
    };
  }

  /**
   * Connect to a media stream.
   **/
  connectToSource(stream) {
    if (!this.context || stream.getAudioTracks().length < 1) {
      return this;
    }
    Logger.debug('SoundMeter::connectToSource', stream);
    this.mic = this.context.createMediaStreamSource(stream);
    this.mic.connect(this.script);
    this.script.connect(this.context.destination);
    return this;
  }

  /**
   * Stop read interval and disconnect from stream.
   **/
  stop() {
    if (!this.context) {
      return;
    }

    this.offUpdate();
    if (this.mic) {
      this.mic.disconnect();
    }
    this.script.disconnect();
    if (this.context.close) {
      this.context.close();
    }
  }

  offUpdate() {
    window.clearInterval(this.audioMeterInterval);
    window.clearInterval(this.readInterval);
  }

  onUpdate(listener) {
    if (!this.context) {
      return;
    }

    if (this.audioMeterInterval) {
      clearInterval(this.audioMeterInterval);
    }
    this.readInterval = setInterval(
      this.readAudioLevel.bind(this, listener),
      SoundMeter.updateInterval
    );
  }

  /**
   * Read audio level. Avoids triggering a re-render of the device-dialog
   * when the change is below 1% which wouldn't be noticable anyways.
   *
   * NOTE: When an USB Microphone is abruptly disconnected. The
   * intervalCounter reacts: when switching to a different mic, it seems to
   * take a bit to not return 0.00 for the audio level.
   **/
  /* eslint-disable max-statements */
  readAudioLevel(update) {
    let audioLevel = this.level;

    // Handle microphone disconnect
    if (audioLevel === 0 && this.instant === 0 && this.intervalCounter > 5) {
      this.silenceDuration++;
      if (this.silenceDuration > 25) {
        debounce(() => {
          update({ error: 'EyesonMicrophoneError' });
        }, 1000);
      }
    }

    // Clear a MicrophoneError once we get a signal, intervalCounter might be
    // too low sometimes.
    if (this.level !== this.instant) {
      this.silenceDuration = 0;
      let diff = Math.abs(audioLevel - this.instant);
      audioLevel = this.instant;

      if (audioLevel > 0.7 || diff > 1) {
        this.level = this.instant;
        update({ value: this.level });
      }
    }

    this.intervalCounter++;
  }
  /* eslint-enable max-statements */
}

SoundMeter.updateInterval = 100;

export default SoundMeter;
