/* global FB */

const LOAD_ERRORS = ['FB is not defined', 'FB.login is not a function'];

/**
 * Facebook API.
 **/
class FacebookApi {
  /* eslint-disable max-statements */
  constructor(eyeson) {
    this.eyeson = eyeson;
    this.apiKey = eyeson.config.facebook.key;
    this.stream = null;
    this.permalLinkUrl = 'default-permalink-url';

    if (!this.apiKey) {
      return;
    }

    this.sendStop = this.sendStop.bind(this);
    this.loggedIn = this.loggedIn.bind(this);
    this.fetchLink = this.fetchLink.bind(this);
    this.startStream = this.startStream.bind(this);
    this.handleError = this.handleError.bind(this);
    this.streamPublished = this.streamPublished.bind(this);

    this.initFacebookApi();
  }
  /* eslint-enable max-statements */

  initFacebookApi() {
    if (!document || typeof FB !== 'undefined') {
      return;
    }

    window.fbAsyncInit = () => {
      FB.init({
        appId: this.apiKey,
        xfbml: true,
        version: 'v2.9',
        autoLogAppEvents: true
      });
      FB.AppEvents.logPageView();
    };

    /* eslint-disable */
    (function(d, s, id) {
      var js,
        fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) {
        return;
      }
      js = d.createElement(s);
      js.id = id;
      js.src = '//connect.facebook.net/en_US/sdk.js';
      fjs.parentNode.insertBefore(js, fjs);
    })(document, 'script', 'facebook-jssdk');
    /* eslint-enable */
  }

  /**
   * Start live stream process here.
   **/
  activate() {
    FB.ui(
      {
        display: 'popup',
        method: 'live_broadcast',
        phase: 'create'
      },
      this.startStream
    );
  }

  /**
   * Inovked _after_ activation.
   **/
  publish() {
    FB.ui(
      {
        display: 'popup',
        method: 'live_broadcast',
        phase: 'publish',
        broadcast_data: this.stream // eslint-disable-line camelcase
      },
      this.streamPublished
    );
  }

  /**
   * Inovked _after_ publish.
   **/
  login() {
    try {
      FB.login(this.loggedIn, { scope: 'user_videos' });
    } catch (error) {
      this.handleError(this.getError(error));
    }
  }

  /**
   * Deactivate the stream.
   **/
  deactivate() {
    this.stream = null;
    this.sendStop();
  }

  /**
   * Wrapper, only send stop when we have a stream.
   **/
  sendStop() {
    return this.stream ? this.eyeson.send({ type: 'stop_facebook' }) : null;
  }

  getError(response) {
    let error = { result: { error: { message: 'user closed popup' } } };
    if (response && response.error_message) {
      error.result.error.message = response.error_message;
      return error;
    }
    if (
      response &&
      (!response.message || LOAD_ERRORS.includes(response.message))
    ) {
      error.result.error = {
        name: 'facebook:not_loaded',
        message: 'facebook api did not load'
      };
    }
    return error;
  }

  startStream(stream) {
    if (!stream || stream.error_message) {
      this.handleError(this.getError(stream));
      return;
    }
    this.stream = stream;
    this.eyeson.send({
      type: 'start_facebook',
      streamUrl: stream.secure_stream_url
    });
  }

  streamPublished(response) {
    if (!response || response.error_message) {
      this.handleError(this.getError(response));
      return;
    }
    this.fetchLink();
    this.eyeson.send({ type: 'facebook_published' });
  }

  loggedIn(response) {
    // If someone closes the popup or cancels we actually get undefined for
    // authResponse & status
    if (!response.authResponse && !response.status) {
      this.handleError(this.getError(response));
      return;
    }

    this.permalLinkUrl = `https://facebook.com/${response.authResponse.userID}`;
    this.eyeson.send({ type: 'facebook_logged_in' });
  }

  fetchLink() {
    FB.api(`/${this.stream.id}?fields=permalink_url`, response => {
      // fallback to the users profile page, in case we don't get a
      // permalink_url
      if (!response.permalink_url || response.error_message) {
        response.permalink_url = this.permalLinkUrl; // eslint-disable-line camelcase
      }

      let playerUrl = response.permalink_url.includes('https://facebook.com')
        ? response.permalink_url
        : `https://facebook.com${response.permalink_url}`;

      this.eyeson.send({
        type: 'publish_broadcast',
        playerUrl: playerUrl
      });
    });
  }

  handleError(response) {
    if (!response.result.error.name) {
      response.result.error.name = 'facebook:closed';
    }
    this.sendStop();
    this.eyeson.send({
      type: 'handle_facebook_error',
      error: response.result.error
    });
  }
}

export default FacebookApi;
