/* eslint-disable max-lines */
import Logger from './Logger';
import FeatureDetector from './FeatureDetector';
import 'webrtc-adapter';
import {
  stopTrack,
  getCanvasTracks,
  isCanvasPresentationStream
} from './utils/StreamHelpers';

/**
 * SessionDescriptionHandler
 * This class implements a couple of methods that SIP.js expects.
 * For more details see:
 * https://sipjs.com/api/0.11.0/sessionDescriptionHandler/
 */
class SessionDescriptionHandler {
  constructor(session, options) {
    this.options = options || {};
    this.session = session;
    this.connection =
      session.passedOptions.sessionDescriptionHandlerOptions.connection;
    this.CONTENT_TYPE = 'application/sdp';
    this.initPeerConnection();
  }

  /**
   * Gets the local description from the underlying media implementation
   */
  getDescription(options, modifiers) {
    Logger.debug(
      'SessionDescriptionHandler::getDescription:',
      options,
      modifiers
    );
    this.localStream = options.stream;

    this.localStream.getTracks().forEach(track => {
      this.peerConnection.addTrack(track, this.localStream);
    });

    return this.createOfferOrAnswer(options.RTCConstraints, modifiers).then(
      sdp => {
        return { body: sdp, contentType: this.CONTENT_TYPE };
      }
    );
  }

  /**
   * Set the remote description to the underlying media implementation
   */
  setDescription(sessionDescription, options, modifiers) {
    Logger.debug(
      'SessionDescriptionHandler::setDescription:',
      sessionDescription,
      options,
      modifiers
    );

    const description = new RTCSessionDescription({
      type: this.hasOffer('local') ? 'answer' : 'offer',
      sdp: sessionDescription
    });

    return this.peerConnection.setRemoteDescription(description).then(() => {
      this.session.passedOptions.sessionDescriptionHandlerOptions.remoteDescriptionUpdate(
        description
      );
    });
  }

  /**
   * Check if the Session Description Handler can handle the Content-Type
   * described by a SIP Message
   */
  hasDescription(contentType) {
    Logger.debug('SessionDescriptionHandler::hasDescription:', contentType);
    return contentType === this.CONTENT_TYPE;
  }

  /**
   * This accessor returns the direction of flow for the Session Description
   * Handler. It is intended to be used as a hold indicator.
   */
  getDirection() {
    Logger.warn('SessionDescriptionHandler::getDirection: NOT IMPLEMENTED');
  }

  /**
   * The modifier that should be used when the session would like to place
   * the call on hold
   */
  holdModifier(description) {
    Logger.warn(
      'SessionDescriptionHandler::holdModifier: NOT IMPLEMENTED.',
      description
    );
  }

  close() {
    Logger.debug('SessionDescriptionHandler::close');
    if (!this.peerConnectionClosed()) {
      this.stopAllTracks();
      this.peerConnection.close();
      clearTimeout(this.iceCheckingTimeout);
      Logger.debug(
        'SessionDescriptionHandler::close ' +
          'Stopped streams and closed peerConnection.'
      );
    }
  }

  /**
   * Starting here, we have "Helper" methods. That is to say: methods not
   * expected by and for SIP.js
   */
  initPeerConnection() {
    const opts = this.buildPeerConnectionOptions();
    Logger.debug('SessionDescriptionHandler::initPeerConnection with: ', opts);
    this.onIceCompleted = new Promise((resolve, reject) => {
      this.resolveIceCompleted = resolve;
      this.rejectIceCompleted = reject;
    });
    try {
      this.peerConnection = new RTCPeerConnection(opts);
      this.peerConnection.ontrack = this.handleOntrack.bind(this);
      this.peerConnection.onicecandidate = this.gotIceCandidate.bind(this);
      this.peerConnection.onnegotiationneeded = this.handleNegotiation.bind(
        this
      );
      this.startIceCheckingTimer(opts.iceCheckingTimeout);
    } catch (error) {
      Logger.error(
        'SessionDescriptionHandler::initPeerConnection failed:',
        error,
        this.connection.user
      );
    }
  }

  peerConnectionClosed() {
    return Boolean(
      this.peerConnection && this.peerConnection.signalingState === 'closed'
    );
  }

  /**
   * Only signal (handleAccept) that we have a remote stream once ontrack
   * says so. Is called twice, once for audio and once for video track.
   */
  handleOntrack(event) {
    [this.remoteStream] = event.streams;
    Logger.debug(
      'SessionDescriptionHandler::handleOntrack: ',
      event.track.kind
    );
    event.track.onunmute = () => {
      this.session.passedOptions.sessionDescriptionHandlerOptions.handleUnmute(
        event.track
      );
    };

    this.session.passedOptions.sessionDescriptionHandlerOptions.handleAccept(
      this.remoteStream
    );
  }

  startIceCheckingTimer(iceCheckingTimeout) {
    this.iceCheckingTimer = setTimeout(() => {
      Logger.debug(
        'iceCheckingTimeout triggered after: ' +
          iceCheckingTimeout +
          ' milliseconds'
      );
      this.resolveIceCompleted();
    }, iceCheckingTimeout);
  }

  gotIceCandidate(event) {
    const { candidate } = event;

    if (!candidate) {
      Logger.debug(
        'SessionDescriptionHandler::gotIceCandidate: ' +
          'no candidates left, onIceCompleted.'
      );
      this.resolveIceCompleted();
      return;
    }

    if (candidate.type === 'relay' && candidate.sdpMid === 'video') {
      Logger.debug(`SessionDescriptionHandler::gotIceCandidate: ${candidate}.
                   Relay candidate found, onIceCompleted.`);
      this.resolveIceCompleted();
      return;
    }

    Logger.debug(`SessionDescriptionHandler::gotIceCandidate: ${candidate}`);
  }

  /**
   * We need to re-format the iceServers here. SIP.js used to offer a
   * higher level api and we supplied the servers in a different format e.g:
   *
   * { stunServers: ["stun:stun1.visocon.com:3478"] }
   * { turnServers: [{ urls: [1,2,3], username: "bob", password: "nob" }] }
   *
   * We currently have the api set return the creds in that format.
   * The peerConnection however expects:
   *
   * {
   *   "iceServers": [
   *     { "urls": "stun:stun1.visocon.com:3478" },
   *     { "urls": [1,2,3], "username": "bob", "credential": "nob" }
   *   ]
   * }
   *
   * Note: Edge is a little picky when it comes to turn server urls. We need
   * to specify the protocol via (`?transport=udp`) for edge to swallow it.
   * But we don't want to add additional turn-server for the rest of the
   * browsers, since FF gives us a warning "Things will go wrong when providing
   * more than 5 urls". We also don't remove any turn urls, since adapter and
   * edge will be doing that for us.
   */
  buildPeerConnectionOptions() {
    const tmp = Object.assign({}, this.connection.user);
    tmp.stunServers = { urls: tmp.stunServers };
    tmp.turnServers = tmp.turnServers.map(server => {
      if (FeatureDetector.isEdge()) {
        const urlWithoutProtocol = server.urls.find(
          url => !url.match(/transport/)
        );
        server.urls.push(urlWithoutProtocol + '?transport=udp');
      }
      server.credential = server.password;
      return server;
    });
    return Object.assign({}, this.options.peerConnectionOptions, {
      sdpSemantics: 'unified-plan',
      iceServers: tmp.turnServers.concat(tmp.stunServers)
    });
  }

  createOfferOrAnswer(RTCOfferOptions, modifiers) {
    Logger.debug(
      'SessionDescriptionHandler::createOfferOrAnswer: ',
      RTCOfferOptions,
      modifiers
    );

    const offerOptions = RTCOfferOptions || {};
    const sdpModifiers = modifiers || [];

    const methodName = this.hasOffer('remote') ? 'createAnswer' : 'createOffer';

    return this.peerConnection[methodName](offerOptions)
      .then(sdp => this.peerConnection.setLocalDescription(sdp))
      .then(() => this.onIceCompleted)
      .then(() => {
        let { sdp } = this.peerConnection.localDescription;
        try {
          sdpModifiers.forEach(modifier => {
            sdp = modifier(sdp);
          });
        } catch (error) {
          Logger.error('sdpModifier failed: ', error, sdp);
        }
        return sdp;
      })
      .catch(error => {
        Logger.error('createOfferOrAnswer:', error);
      });
  }

  hasOffer(type) {
    return this.peerConnection.signalingState === `have-${type}-offer`;
  }

  getLocalStream() {
    return this.localStream;
  }

  getRemoteStream() {
    return this.remoteStream;
  }

  setStream(newStream) {
    return new Promise((resolve, reject) => {
      let oldStream = this.getLocalStream();
      this.localStream = newStream;

      this.stopUnusedTracks(oldStream, newStream);

      let [audioTrack] = newStream.getAudioTracks();
      // in case of multiple streams, the preferred video track is the
      // canvas presentation stream
      let [videoTrack] = isCanvasPresentationStream(newStream)
        ? getCanvasTracks(newStream)
        : newStream.getVideoTracks();

      if (!this.tracksExist()) {
        resolve({ newStream: newStream, remoteStream: this.remoteStream });
      }

      const audioSender = this.peerConnection
        .getSenders()
        .find(sender => sender.track.kind === 'audio');
      const videoSender = this.peerConnection
        .getSenders()
        .find(sender => sender.track.kind === 'video');

      audioSender
        .replaceTrack(audioTrack)
        .then(() => videoSender.replaceTrack(videoTrack))
        .then(() => {
          resolve({ newStream: newStream, remoteStream: this.remoteStream });
        })
        .catch(reject);
    });
  }

  stopUnusedTracks(oldStream, newStream) {
      const unusedTracks = oldStream
              .getTracks()
              .filter(track => !newStream.getTracks().includes(track));
      unusedTracks.forEach(stopTrack);
  }

  tracksExist() {
    return this.peerConnection
                .getSenders()
                .find(sender => sender.track !== null) > 0;
  }

  handleNegotiation() {
    Logger.debug('SessionDescriptionHandler::handleNegotiation');
  }

  /**
   * Stop all tracks
   */
  stopAllTracks() {
    Logger.debug('SessionDescriptionHandler::stopAllTracks');
    let receivers = this.peerConnection.getReceivers
      ? this.peerConnection.getReceivers()
      : [];
    this.peerConnection
      .getSenders()
      .concat(receivers)
      .forEach(rtp => {
        if (rtp.track) {
          stopTrack(rtp.track);
        }
      });
  }
}

export default SessionDescriptionHandler;
/* eslint-enable max-lines */
