// Due to transport errors in SIP 0.15 we're currently back to 0.11
// https://github.com/onsip/SIP.js/issues/706
// https://github.com/onsip/SIP.js/issues/727
// import { UA } from 'sip.js';
import SIP from 'sip.js';
import Logger from './Logger';
import WHITELIST from './SipMessageWhitelist';
import SipMessageTransformer from './SipMessageTransformer';
import SessionDescriptionHandler from './SessionDescriptionHandler';

const factoryOptions = { peerConnectionOptions: { iceCheckingTimeout: 3000 } };

/**
 * eyeson Sip Connection.
 *
 * Registers with provided sip credentials and handles events received.
 **/
class SipConnection {
  constructor(user, room) {
    this.user = user;
    this._room = room;
    this.user.sessionDescriptionHandlerFactoryOptions = factoryOptions;
    this.user.sessionDescriptionHandlerFactory = (session, options) =>
      new SessionDescriptionHandler(session, options);

    const uaOptions = this.adjustUserAgentOptions(this.user);
    // this.userAgent = new UA(uaOptions);
    this.userAgent = new SIP.UA(uaOptions);
    this.transformer = new SipMessageTransformer(this.user);
    this.start = this.start.bind(this);
  }

  /**
   * SIP.js introduced transportOptions somewhere after 0.9.0.
   **/
  adjustUserAgentOptions(user) {
    user.autostart = false;
    user.transportOptions = { wsServers: [user.wsServers], traceSip: true };
    return user;
  }

  /**
   * Connect and register to the configured WebSocket server.
   **/
  start() {
    return new Promise((resolve, reject) => {
      this.userAgent.once('transportCreated', transport => {
        transport.on('transportError', () => {
          reject(new Error('SipTransportError'));
        });
        transport.on('connected', () => resolve());
      });
      this.userAgent.start();
    });
  }

  /**
   * Start a session on current connection.
   **/
  startSession(options, modifiers) {
    return this.userAgent.invite(this._room.uri, options, modifiers);
  }

  /**
   * Stop listening for messages.
   **/
  close() {
    if (!this.userAgent) {
      return;
    }
    this.userAgent.removeAllListeners('message');
    this.userAgent.unregister();
    this.userAgent = null;
  }

  /**
   * Parse, filter, transform and finally forward messages received from SIP
   * connection.
   **/
  onMessage(callback) {
    this.userAgent.on('message', response => {
      Logger.debug('SipConnection::onMessage', response);
      const message = JSON.parse(response.body);

      if (!WHITELIST.includes(message.type)) {
        Logger.debug(
          `SipConnection::onMessage ignoring "${message.type}" message.`
        );
        return;
      }

      const transformedMessage = this.transformer.process(message);
      callback(transformedMessage);
    });
  }

  /**
   * Transport a message over current connection.
   **/
  send(msg) {
    msg.cid = this.user.uri;
    if (!this.userAgent) {
      return;
    }
    this.userAgent.message(this._room.uri, JSON.stringify(msg));
  }
}

export default SipConnection;
