import BaseEvent from './BaseEvent';

class StartPresentingEvent extends BaseEvent {
  async handle({ stream = null }) {
    let presentationStream = stream || this.context._session.localStream;
    if (
      this.context._session.canvasMixer &&
      this.context._session.canvasMixer.stream
    ) {
      // eslint-disable-next-line prefer-destructuring
      presentationStream = this.context._session.canvasMixer.stream;
    }
    await this.context._session.setStream(presentationStream);
    this.context._session.send({ type: 'set_presenter', on: true });
    this.context._session.send({ type: 'desktopstreaming', on: true });
    this.context._session.emit({ type: 'presentation_started' });
  }
}

export default StartPresentingEvent;
