import Logger from '../Logger';
import BaseEvent from './BaseEvent';
import MediaStreamBuilder from '../MediaStreamBuilder';

class StartStreamEvent extends BaseEvent {
  handle({ audio = true, video = true }) {
    Logger.debug(`StartStreamEvent::handle audio: ${audio} video: ${video}`);
    const { _session } = this.context;
    new MediaStreamBuilder({ audio: audio, video: video })
      .start()
      .then(_session.setStream)
      .catch(Logger.error);
  }
}

export default StartStreamEvent;
