import Logger from './../Logger';
import BaseEvent from './BaseEvent';

/**
 * HandleStreamAnswerEvent
 *
 * Update our remote description with provided answer.
 **/
class HandleStreamAnswerEvent extends BaseEvent {
  handle(msg) {
    Logger.debug('HandleStreamAnswerEvent: ', msg);
    const pc = this.context._session.peerConnection;

    if (pc.signalingState === 'stable') {
      return;
    }

    pc
      .setRemoteDescription(new RTCSessionDescription(msg.sdp))
      .then(Logger.debug('HandleStreamAnswerEvent: set remote description'))
      .then(() => {
        this.context._session.remoteDescriptionUpdate(msg.sdp);
      })
      .catch(error => Logger.warn('HandleStreamAnswerEvent: ', error, msg.sdp));
  }
}

export default HandleStreamAnswerEvent;
