import BaseEvent from './BaseEvent';

class StopPresentingEvent extends BaseEvent {
  handle() {
    if (this.context._session.canvasMixer) {
      this.context._session.canvasMixer.stop();
    }
    this.context._session.send({ type: 'desktopstreaming', on: false });
    this.context._session.send({ type: 'set_presenter', on: false });
    this.context._session.emit({ type: 'presentation_ended' });
  }
}

export default StopPresentingEvent;
