import Logger from './../Logger';
import PublicEvent from './PublicEvent';

class StopFacebookEvent extends PublicEvent {
  handle(msg) {
    this.context._api
      .stopBroadcast('facebook')
      .then(() => super.handle({ type: msg.type }))
      .catch(error => Logger.warn(error));
  }
}

export default StopFacebookEvent;
