import Logger from './Logger';

import { StopPlaybackEvent, StartPlaybackEvent } from './events/PlaybackEvents';

import {
  StartAnnotationEvent,
  StopAnnotationEvent
} from './events/AnnotationEvents';

import StfuEvent from './events/StfuEvent';
import IgnoreEvent from './events/IgnoreEvent';
import PublicEvent from './events/PublicEvent';
import PodiumEvent from './events/PodiumEvent';
import ForwardEvent from './events/ForwardEvent';
import SnapshotEvent from './events/SnapshotEvent';
import SetLayerEvent from './events/SetLayerEvent';
import SendChatEvent from './events/SendChatEvent';
import SetLayoutEvent from './events/SetLayoutEvent';
import RoomReceivedEvent from './events/RoomReceivedEvent';
import FetchRoomEvent from './events/FetchRoomEvent';
import DeleteFileEvent from './events/DeleteFileEvent';
import UploadFileEvent from './events/UploadFileEvent';
import ReceiveChatEvent from './events/ReceiveChatEvent';
import StopYouTubeEvent from './events/StopYouTubeEvent';
import StartStreamEvent from './events/StartStreamEvent';
import ChangeStreamEvent from './events/ChangeStreamEvent';
import ToggleCameraEvent from './events/ToggleCameraEvent';
import StartYouTubeEvent from './events/StartYouTubeEvent';
import StopFacebookEvent from './events/StopFacebookEvent';
import AcceptSessionEvent from './events/AcceptSessionEvent';
import StartFacebookEvent from './events/StartFacebookEvent';
import StopRecordingEvent from './events/StopRecordingEvent';
import VoiceActivityEvent from './events/VoiceActivityEvent';
import StartRecordingEvent from './events/StartRecordingEvent';
import StartScreenCaptureEvent from './events/ScreenCaptureEvent';
import StartPresentingEvent from './events/StartPresentingEvent';
import StopPresentingEvent from './events/StopPresentingEvent';
import StopBroadcastsEvent from './events/StopBroadcastsEvent';
import ClearFrontLayerEvent from './events/ClearFrontLayerEvent';
import PublishBroadcastEvent from './events/PublishBroadcastEvent';
import RequestGuestUserEvent from './events/RequestGuestUserEvent';
import HandleStreamOfferEvent from './events/HandleStreamOfferEvent';
import HandleStreamAnswerEvent from './events/HandleStreamAnswerEvent';
import ParticipantListUpdateEvent from './events/ParticipantListUpdateEvent';
import { StartMixerEvent, UpdateMixerEvent } from './events/MixerEvents';
import LockMeetingEvent from './events/LockMeetingEvent';

/**
 * Event Handler uses the connection credentials for the room and
 * the user to setup a (monitored) connection, start a session and
 * listen for any events.
 **/
class EventHandler {
  constructor() {
    this.listeners = [];
    this._eyeson = null;
    this._session = null;
    this._monitor = null;
    this._connection = null;
    this._rtConnection = null;

    this.send = this.send.bind(this);
  }

  /* eslint-disable max-statements */
  destroy() {
    if (this._monitor) {
      this._monitor.destroy();
    }
    if (this._connection) {
      this._connection.close();
    }
    if (this._rtConnection) {
      this._rtConnection.close();
    }
    if (this._session) {
      this._session.end();
    }
    this._api = null;
    this._session = null;
    this._monitor = null;
    this._connection = null;
    this._rtConnection = null;
  }
  /* eslint-enable max-statements */

  set connection(con) {
    if (this._connection) {
      Logger.error(
        'A connection is already set. If you are trying to start a new' +
          ' session you have to end the current one first.'
      );
      return;
    }
    this._connection = con;
    this._connection.onMessage(this.send);
  }

  set rtConnection(con) {
    if (this._rtConnection) {
      Logger.error(
        'A real-time connection is already set. If you are trying to start' +
          ' a new session you have the close the current before.'
      );
      return;
    }
    this._rtConnection = con;
    this._rtConnection.onMessage(this.send);
  }

  set monitor(mon) {
    if (this._monitor) {
      Logger.error(
        'A monitor is already set. If you are trying to start a new' +
          ' session you have to end the current one first.'
      );
      return;
    }
    this._monitor = mon;
    this._monitor.onError(this.send);
  }

  set session(ses) {
    if (this._session) {
      Logger.error(
        'A session is already set. If you are trying to start a new' +
          ' session you have to end the current one first.'
      );
      return;
    }
    this._session = ses;
    this._session.onEvent(this.send);
  }

  set api(api) {
    this._api = api;
  }

  set rtData(data) {
    this._rtData = data;
  }

  set eyeson(eyeson) {
    this._eyeson = eyeson;
  }

  get monitor() {
    return this._monitor;
  }

  /**
   * Register a new listener.
   **/
  onReceive(callback) {
    Logger.debug('EventHandler::onReceive');
    this.listeners.push(callback);
  }

  /**
   * Unregisters a listener.
   **/
  removeListener(callback) {
    this.listeners = this.listeners.filter(listener => listener !== callback);
  }

  /**
   * On receiving an event, notify all registered listeners.
   **/
  /* eslint-disable camelcase */
  send(msg) {
    if (!msg.silenced) {
      Logger.debug('EventHandler::send', msg);
    }
    new ({
      stfu: ForwardEvent,
      chat: ReceiveChatEvent,
      accept: AcceptSessionEvent,
      podium: PodiumEvent,
      snapshot: SnapshotEvent,
      send_chat: SendChatEvent,
      set_layer: SetLayerEvent,
      set_layout: SetLayoutEvent,
      fetch_room: FetchRoomEvent,
      room_ready: RoomReceivedEvent,
      room_setup: RoomReceivedEvent,
      memberlist: ParticipantListUpdateEvent,
      upload_file: UploadFileEvent,
      delete_file: DeleteFileEvent,
      request_stfu: StfuEvent,
      stream_offer: HandleStreamOfferEvent,
      stop_youtube: StopYouTubeEvent,
      stream_answer: HandleStreamAnswerEvent,
      start_youtube: StartYouTubeEvent,
      change_stream: ChangeStreamEvent,
      toggle_camera: ToggleCameraEvent,
      stop_facebook: StopFacebookEvent,
      stop_playback: StopPlaybackEvent,
      playback_update: ForwardEvent,
      start_playback: StartPlaybackEvent,
      start_stream: StartStreamEvent,
      start_facebook: StartFacebookEvent,
      stop_recording: StopRecordingEvent,
      start_screen_capture: StartScreenCaptureEvent,
      stop_broadcasts: StopBroadcastsEvent,
      start_recording: StartRecordingEvent,
      stop_presenting: StopPresentingEvent,
      start_presenting: StartPresentingEvent,
      stop_annotation: StopAnnotationEvent,
      start_annotation: StartAnnotationEvent,
      publish_broadcast: PublishBroadcastEvent,
      clear_front_layer: ClearFrontLayerEvent,
      request_guest_user: RequestGuestUserEvent,
      voice_activity_raw: VoiceActivityEvent,
      start_mixer: StartMixerEvent,
      update_mixer: UpdateMixerEvent,
      lock_meeting: LockMeetingEvent,

      capture_error: ForwardEvent,
      voice_activity: ForwardEvent,
      options_update: ForwardEvent,
      ext_not_installed: ForwardEvent,
      broadcasts_update: ForwardEvent,
      facebook_logged_in: ForwardEvent,
      facebook_published: ForwardEvent,
      presentation_ended: ForwardEvent,
      handle_youtube_error: ForwardEvent,
      handle_facebook_error: ForwardEvent,
      status_update_youtube: ForwardEvent,

      recording: IgnoreEvent,
      ext_cancel: IgnoreEvent,
      ext_installed: IgnoreEvent,
      message_status: IgnoreEvent,
      moderator_info: IgnoreEvent
    }[msg.type] || PublicEvent)(this).handle(msg);
  }
  /* eslint-enable camelcase */
}

export default EventHandler;
