import BaseEvent from './BaseEvent';

class StartFacebookEvent extends BaseEvent {
  handle(msg) {
    const { _api, _session } = this.context;

    _api
      .startBroadcast(msg, 'facebook')
      .catch(error =>
        _session.emit({
          type: 'start_broadcast_error',
          platform: 'facebook',
          error: error
        })
      );
  }
}

export default StartFacebookEvent;
